import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	components:{
		uniLoadMore
	},
	data(){
		return{
			uid:'',
			token:'',
			active:1,
			cartList:[
				{
					
				}
			],
			sh:'',
			dataList:[],
			page:1,
			pageSize:8,
			total:0,
			more:''
		}
	},
	onLoad(p){
		var that=this
		var userInfo=uni.getStorageSync('userInfo'),that=this
		if(p.active!=0){
			this.active=p.active
		}
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
		}
		this.getList(this.page)
		setTimeout(function(){
			that.$getHeight('#sv',(res) =>{
				that.sh=res
			})
		},0)
		uni.$on('updateOrder',() =>{
			this.reset()
			this.getList(this.page)
		})
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	methods:{
		toDetail(){
			uni.navigateTo({
				url:'./myOrderDetail'
			})
		},
		toShop(id){
			uni.navigateTo({
				url:`/pages/bussiness/shopPreview?id=${id}`
			})
		},
		toGoodsDetail(id){
			uni.navigateTo({
				url:`/pages/index/goodsDetail?id=${id}`
			})
		},
		topay(item){
			uni.navigateTo({
				url:`/pages/index/cashier?orderId=${item.orderno}&money=${item.totalprice}`
			})
		},
		toComment(item){
			uni.navigateTo({
				url:'./comment?item='+JSON.stringify(item)
			})
		},
		toDrawback(item){
			var goods=[]
			item.goods.forEach((item1) =>{
				item1.goodsdata.forEach((item2) =>{
					goods.push(item2)
				})
			})
			uni.navigateTo({
				url:`/pages/drawback/chooseWay?goods=${JSON.stringify(goods)}&orderno=${item.orderno}`
			})
		},
		toggle(t){
			this.active=t
			this.reset()
			this.getList(this.page)
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
		},
		getList(p){
			var that=this
			var params={
			  page:p,
			  pagesize: this.pageSize,
			  uid:this.uid,
			  token:this.token,
			  status:this.active
			}
			if(this.page==1){
				this.$loading()
			}
			  var url='&r=api.member.order'
			  this.$apiPost(url,params).then((res) =>{
				  that.total=res.total
				  that.dataList=that.dataList.concat(res.data)
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page)
		  // },2000)
		},
		addCollect(item){
			var that=this,ids=[]
			item.forEach((items) =>{
				ids.push(items.goodsid)
			})
			var params={
			  uid:this.uid,
			  token: this.token,
				goodsid:ids.join(',')
			}
			  var url='&r=api.member.cart.add'
			  this.$apiPost(url,params).then((res) =>{
					// that.options[2].info++
									that.$msg('添加成功')
			  })
		},
		cancelOrder(order,from){
			var that=this
			var params={
			  uid:this.uid,
			  token: this.token,
				orderno:order
			}
			  var url='&r=api.member.order.cancel'
			  this.$apiPost(url,params).then((res) =>{
				  this.$msg('取消成功')
					// that.options[2].info++
					that.dataList.splice(from,1)
			  })
		},
		confirmReceive(order){
			var that=this
			var params={
			  uid:this.uid,
			  token: this.token,
				orderno:order
			}
			  var url='&r=api.member.order.receive'
			  this.$apiPost(url,params).then((res) =>{
				  this.$msg('确认收货成功')
				  this.reset()
				  this.getList(this.page)
					// that.options[2].info++
			  })
		}
	}
}
